import Vue from 'vue'

const ZH = {
  'confirm': '',
  'cancel': '',
  'ok': '',

  'loading': '',
  'success': '',
  'error': '',

  'prePage': '',
  'nextPage': ''
}

const EN = {
  'confirm': 'Confrim',
  'cancel': 'Cancel',
  'ok': 'Ok',

  'loading': 'Loading',
  'success': 'Success',
  'error': 'Error',

  'prePage': 'Pre',
  'nextPage': 'Next'
}

let vm = new Vue({
  data: {
    lang: ZH
  }
})

export default {
  set (option) {
    if (option == 'en') {
      vm.lang = EN;
    }

    if (option == 'zh') {
      vm.lang = ZH;
    }
  },

  get () {
    return vm
  }
}



